import Carsousel from "react-elastic-carousel";
import CarouselCard from "./CarouselElastic";
import "./Carousel.css"
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import BannersCarousel from "./BannersCarousel";

export default function Banners(props){
      const breakPoints=[
        {width:1, itemsToShow:1}
    ]

    return(
        <div className="App1">
            <Carsousel breakPoints={breakPoints}>
                <BannersCarousel Banner="bannerNew1"/>
                <BannersCarousel Banner="bannerNew2"/>
                <BannersCarousel Banner="bannerNew3"/>
            </Carsousel>

        </div>
        
    )
}