import HttpConnection from "./HttpConnection";

import TabulatorGrid from "../../../StoreOwner/TabulatorGrid";

const productService = {};

const url = "https://addtomycart.herokuapp.com/pvc/product/";

var saveres = [];
productService.save = async (insert) => {
  for (let i = 0; i < insert.length; i++) {
    await HttpConnection.post(url, insert[i], {
      headers: { Accept: "application/json" },
    })
      .then((response) => {
        saveres = response;
        console.log("product data->" + response.status);
        //TODO check for success otherwise set error and push to error page
        if ("success" === response.data.response) {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );

          //Load data again
        } else {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );
        }
      })
      .catch((error) => {
        console.log(error);
        setError("Please try after some time.");
      });
  }

  console.log("save", saveres);
  return saveres;
};
function setError(error) {
  console.log(error);
}
var updateres = [];
productService.update = async (data) => {
  for (let i = 0; i < data.length; i++) {
    console.log("update", data[0]);
    let id = data[i].id;
    await HttpConnection.put(url + id, data[i], {
      headers: { Accept: "application/json" },
    })
      .then((response) => {
        updateres = response;
        console.log("product data->" + response.data);
        //TODO check for success otherwise set error and push to error page
        if ("success" === response.data.response) {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );

          //Load data again
          TabulatorGrid.newget();
        } else {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );
        }
      })
      .catch((error) => {
        console.log(error);
        setError("Please try after some time.");
      });
  }
  return updateres;
};

productService.delete = async (data) => {
  for (let i = 0; i < data.length; i++) {
    let id = data[i].id;
    var res = [];
    console.log("deletion id", id);
    function del() {}
    await HttpConnection.delete(url + id)
      .then((response) => {
        res = response;
        console.log("deleted data->" + res.data);
        //TODO check for success otherwise set error and push to error page
        if ("success" === response.data.response) {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );

          //Load data again
          TabulatorGrid.newget();
        } else {
          setError(
            response.data.response + " " + response.data.exceptionMessage
          );
        }
        return response;
      })
      .catch((error) => {
        console.log(error + JSON.stringify(data[i]));
        setError("Please try after some time.");
      });
  }

  return res;
};

productService.getdata = async () => {
  let url = "https://addtomycart.herokuapp.com/pvc/products";
  let res = await HttpConnection.get(url).catch((err) => console.log(err));

  return res;
};

var saveres = [];
productService.saveOrder = async (insert) => {
  await HttpConnection.post(
    "https://addtomycart.herokuapp.com/oc/order",
    insert,
    {
      headers: { Accept: "application/json" },
    }
  )
    .then((response) => {
      saveres = response;
      console.log("product data->" + response.status);
      //TODO check for success otherwise set error and push to error page
      if ("success" === response.data.response) {
        setError(response.data.response + " " + response.data.exceptionMessage);

        //Load data again
      } else {
        setError(response.data.response + " " + response.data.exceptionMessage);
      }
    })
    .catch((error) => {
      console.log(error);
      setError("Please try after some time.");
    });

  console.log("save", saveres);
  return saveres;
};

export default productService;
