import Carsousel from "react-elastic-carousel";
import CarouselCard from "./CarouselElastic";
import "./Carousel.css"
import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import connection from "../Customer/getdata";

import CircularProgress from "@material-ui/core/CircularProgress";
import Grid from "@material-ui/core/Grid";

import Paper from "@material-ui/core/Paper";

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      justifyContent: "space-around",
    },
    paper: {
      padding: theme.spacing(2),
      textAlign: "center",
  
      color: theme.palette.text.secondary,
    },
  }));

export default function ElasticCarousel(props){
    localStorage.removeItem("mycart");
    const classes = useStyles();
  
    // eslint-disable-next-line react-hooks/rules-of-hooks
    const [data, setdata] = useState({});
  
    localStorage.removeItem("mycart");
    // eslint-disable-next-line react-hooks/rules-of-hooks
    useEffect(() => {
      connection.getdata().then((res) => {
        console.log("data", res.data);
  
        setdata(res.data);
      });
    }, []);
    var cart = [];

    const breakPoints=[
        {width:1, itemsToShow:1},
        {width:500, itemsToShow:2},
        {width:765, itemsToShow:3},
        {width:1200, itemsToShow:4},

    ]
    
    function sendProducts(products) {
        // console.log("len",cart.length)
        // console.log("data",products)
        console.log("type of product", typeof products);
        console.log("counter is", products.counter);
        console.log("cart", JSON.stringify(cart));
    
        if (products.counter > 0 || cart.length > 0) {
          if (cart.length == 0) {
            cart.push(products);
            let temp = { selects: cart };
            localStorage.setItem("mycart", JSON.stringify(temp));
            console.log("products are", products);
          } else {
            for (let i = 0; i < cart.length; i++) {
              console.log("i index", cart[i]);
              if (cart[i].id === products.id) {
                console.log("i index", cart[i]);
                console.log("cart before delete", JSON.stringify(cart));
    
                cart.splice(i, 1);
                cart.push(products);
                let temp = { selects: cart };
                localStorage.setItem("mycart", JSON.stringify(temp));
    
                console.log("products are", products);
              } else {
                console.log("i value, cart len", i, cart.length);
                if (i === cart.length - 1) {
                  cart.push(products);
                  let temp = { selects: cart };
                  localStorage.setItem("mycart", JSON.stringify(temp));
                  console.log("products are", products);
                }
              }
            }
          }
        }
      }
    
    return(
        <div className="App1">
            <Carsousel breakPoints={breakPoints}>
                <CarouselCard caro="Apple"/>
                <CarouselCard caro="Banana"/>
                <CarouselCard caro="Cabbage"/>
                <CarouselCard caro="Cucumber"/>
                <CarouselCard caro="Grapes"/>
                <CarouselCard caro="Kiwi"/>
                <CarouselCard caro="Mango"/>
            </Carsousel>

        </div>
        
    )
}