import "./Footer.css";
import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import PaymentTwoToneIcon from '@material-ui/icons/PaymentTwoTone';
import StorefrontRoundedIcon from '@material-ui/icons/StorefrontRounded';
import PhoneAndroidRoundedIcon from '@material-ui/icons/PhoneAndroidRounded';
import EmailRoundedIcon from '@material-ui/icons/EmailRounded';
import ContactSupportRoundedIcon from '@material-ui/icons/ContactSupportRounded';
import StoreTwoToneIcon from '@material-ui/icons/StoreTwoTone';
import PermContactCalendarIcon from '@material-ui/icons/PermContactCalendar';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    textAlign: "center",
    color: "#FFFFFF",
    marginTop:'20px'
  },
}));

const Footer = () => {
  const classes = useStyles();

  return (
    <div className={classes.root} style={{ backgroundColor: "#cb997e" }}>
      <Grid container spacing={3}>
        <Grid item xs={12} sm={4}style={{textAlign:'center'}}>
          <StoreTwoToneIcon/> ADDRESS<th/>
          <br/>
        <span>STORE NAME</span><tr/>
        <span>STORE OWNER NAME</span><tr/>
        <span>STORE ADDRESS</span><tr/>
        </Grid>

        <Grid item xs={12} sm={4}>
       <PaymentTwoToneIcon/> PAYMENT METHODS<th/>
        <br/>
        <span> PHONE PE</span><tr/>
        <span>GOOGLE PAY</span><tr/>
        <span>CASH ON DELIVERY</span><tr/>
        </Grid>

        <Grid item xs={12} sm={4}>
        <PermContactCalendarIcon/>  CONTACT INFORMATION<th/>
        <br/>
        <span><EmailRoundedIcon/>  exampe123@email.com</span><tr/>
        <span><PhoneAndroidRoundedIcon/>  9199******99</span><tr/>
        <span><StorefrontRoundedIcon/> 9981******88</span><tr/>
        </Grid>
        <Grid item xs={12}>------------------------------------------------------------------------------</Grid>
        <Grid item xs={12}>@Copy Right 2020</Grid>
      </Grid>

    </div>  
  );
};

export default Footer;
